@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap);
* {
	padding: 0;
	margin: 0;
	font-family: 'Montserrat', sans-serif;
}

.l-box {
	margin: 0.5rem;
	border: rgba(85, 85, 85, 0.39) 1px solid;
	height: 200px;
	border-radius: 5px;
	/* box-shadow: 1px 2px 2px rgb(88, 76, 76); */
}

.header {
	height: 10vh;
	width: 100%;
	background-color: rgb(137, 41, 226);
	color: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
}

.header--h1 {
	font-size: 36px;
	font-weight: 100;
}

.form__container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	height: 400px;
}

.room__links--container {
	display: flex;
	align-items: center;
	flex-direction: column;
	height: 400px;
	overflow-y: auto;
}

.form__heading {
	font-weight: 100;
	margin: 20px;
	font-size: 40px;
}

.form__label {
	margin: auto;
}

.form__input {
	margin-left: 20px;
	height: 30px;
	padding: 5px;
}

.form__button {
	margin: auto;
	outline: none;
	background: none;
	height: 50px;
	width: 150px;
	transition: all 0.3s;
	border-radius: 20px;
	box-shadow: 1px 5px 10px rgb(70, 66, 66);
	font-size: 25px;
	font-weight: 400;
	background-color: #fff;
	border: 2px rgb(137, 41, 226) solid;
}

.form__button:hover {
	transform: translateY(-2px);
	box-shadow: 1px 10px 20px rgb(133, 126, 126);
}

.room__link {
	background-color: #fff;
	width: 200px;
	height: 30px;
	padding: 5px;
	margin: 15px;
	border-radius: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	box-shadow: 2px 5px 5px rgb(70, 66, 66);
	transition: all 0.3s;
	text-decoration: none;
	color: purple;
	font-weight: bold;
	font-size: 16px;
}

.room__link:hover {
	transform: translateY(-2px);
	box-shadow: 1px 10px 20px rgb(133, 126, 126);
}

.backdrop {
	position: absolute;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100vw;
	z-index: 500;
	background-color: rgba(0, 0, 0, 0.5);
}

.modal {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	height: 400px;
	width: 700px;
	border-radius: 15px;
	background-color: rgb(236, 232, 232);
	border: 5px rgb(137, 41, 226) solid;
	z-index: 1000;
	box-shadow: 10px 10px 100px rgb(107, 41, 168);
}

@media (max-width: 750px) {
	.modal {
		height: 500px;
		width: 400px;
	}
}

@media (max-width: 450px) {
	.modal {
		height: 400px;
		width: 300px;
	}
}

main {
	height: 90vh;
	width: 100%;
	background-color: rgb(236, 232, 232);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.main--btn {
	outline: none;
	background: none;
	border: 1px rgb(0, 0, 0) solid;
	height: 100px;
	width: 200px;
	margin: 2rem;
	transition: all 0.3s;
	border-radius: 20px;
	box-shadow: 1px 5px 10px rgb(109, 103, 103);
	font-size: 32px;
	font-weight: 300;
}

.main--btn:hover {
	transform: translateY(-2px) scale(1.1);
	box-shadow: 2px 10px 20px rgb(150, 143, 143);
}

.create {
	background-color: rgb(137, 41, 226);
	border: 1px rgb(137, 41, 226) solid;
	color: #fff;
}

.join {
	background-color: #fff;
	border: 5px rgb(137, 41, 226) solid;
}

.post__container {
	display: flex;
	background-color: pink;
	align-items: center;
	flex-grow: 1;
	border-radius: 25px;
	height: 100px;
	margin: 20px 5px;
}

.post__title {
	padding-left: 5px;
	margin-top: -30px;
}

.post__description {
	padding-left: 5px;
}

.post__upVote {
	padding: 20px;
	border: 1px green solid;
	border-radius: 25px;
	width: 20px;
	height: 20px;
	cursor: pointer;
	margin: 10px 10px 10px auto;
}

.post__downVote {
	padding: 20px;
	border: 1px red solid;
	border-radius: 25px;
	height: 20px;
	width: 20px;
	cursor: pointer;
	margin: 10px;
}

.room__container {
	display: flex;
	align-items: center;
	flex-direction: column;
	flex-grow: 1;
}

.room__title {
	text-transform: capitalize;
}

.room__content {
	display: flex;
	flex-direction: row;
	justify-content: center;
	width: 100vw;
}

.room__postList {
	width: 200px;
	margin: 10px 10px 10px auto;
	padding: 15px;
	flex-grow: 0.9;
	border-radius: 20px;
	border: 1px #000 solid;
	height: 70vh;
	overflow: auto;
}

.room__userList {
	width: 200px;
	margin: 10px 10px 10px auto;
	max-height: 80vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: start;
	padding: 15px;
	border: 1px #000 solid;
	border-radius: 20px;
	overflow: auto;
}

.room__btn-add {
	position: absolute;
	bottom: 0;
	right: 0;
	outline: none;
	background: none;
	border: 1px rgb(0, 0, 0) solid;
	height: 40px;
	width: 120px;
	margin: 2rem;
	transition: all 0.3s;
	border-radius: 20px;
	box-shadow: 1px 5px 10px rgb(109, 103, 103);
	font-size: 20px;
	font-weight: 300;
}

.room__btn-add:hover {
	transform: translateY(-2px);
	box-shadow: 2px 10px 20px rgb(150, 143, 143);
}

@media (max-width: 600px) {
	.room__userList {
		display: none;
	}
}

